<template>
  <div class="container">
    <div class="notification is-warning">
      This function is not yet fully implemented.
    </div>

    <nav class="level">
      <div class="level-item has-text-centered">
        <p class="heading">Location</p>
        <p class="title">{{ sentence.citation }}</p>
      </div>
      <div class="level-item has-text-centered">
        <p class="heading">Tokens</p>
        <p class="title">{{ allTokens.length }}</p>
      </div>
      <div class="level-item has-text-centered">
        <p class="heading">Words</p>
        <p class="title">{{ visibleTokens.length }}</p>
      </div>
    </nav>

    <div>
      <p>{{ sentence.text }}</p>
    </div>

    <hr>
    <h3 class="title">Morphology</h3>

    <div class="content box">
      <div class="interlinear">
        <template v-for="token in visibleTokens">
          <div class="intlin">
            <span class="form"> </span>
            <span class="annotation"></span>
          </div>

          <div class="intlin" >
            <span class="form">{{ token.form }}</span>
            <span class="annotation">{{ humanizePartOfSpeech(token.part_of_speech) }}</span>
            <span class="annotation"><em>{{ token.lemma }}</em></span>
          </div>

          <div class="intlin">
            <span class="form space">&nbsp;</span>
            <span class="annotation"></span>
          </div>
        </template>
      </div>
    </div>

    <hr>
    <h3 class="title">Syntax</h3>

    <div class="has-text-centered">
      <object :data="svgURL" type="image/svg+xml"></object>
      <!-- svg id="svg-canvas" width="800" height="600">
        <g/>
      </svg -->
    </div>
  </div>
</template>

<style lang="sass">
.interlinear
  clear: both
  text-align: left

  .intlin
    float: left
    text-align: left
    margin-bottom: 1.2em

    .form
      display: block
      font-size: 1.3em
      text-align: center

    .space
      width: 1em

    .annotation
      display: block
      text-align: center
      color: grey

.interlinear:after
  visibility: hidden
  display: block
  font-size: 0
  content: " "
  clear: both
  height: 0

text
  font-weight: 300
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serf
  font-size: 14px

.box
  // fill: #f00

.node rect
  stroke: #999
  stroke-width: 1px
  fill: #fff

.edgePath path
  stroke: #333
  stroke-width: 1.5px
  fill: none
</style>

<script>
import api from './api'
import filterMixin from './filters'
import annotationSchema from './schema.json'

/* eslint-disable */
function makeGraph(data) {
  var g = new dagreD3.graphlib.Graph({
    multigraph: true,
    compound: true,
  });

  g.setGraph({
    nodesep: 10,
    ranksep: 40,
    rankdir: "TB",
    marginx: 20,
    marginy: 20,
    ranker: "tight-tree"
  });

  g.setNode('ROOT', {  });

  for (var i in data.tokens) {
    var t = data.tokens[i];

    if (t.empty_token_sort === null) {
      g.setNode(t.id, {
        label: t.form,
        shape: 'rect'
      });
    } else {
      g.setNode(t.id, {
        label: t.empty_token_sort,
        shape: 'diamond',
        style: 'fill: #fff; stroke: #999; stroke-width: 1px',
      });
    }

    g.setEdge(t.head_id || 'ROOT', t.id, {
      label: t.relation.toUpperCase(),
      style: 'stroke: orange',
      lineInterpolate: 'basis',
    });
  }

  for (var i in data.tokens) {
    var t = data.tokens[i];
    for (var j in t.slashes) {
      var s = t.slashes[j];
      var relation = s[0].toUpperCase();
      var target = s[1];

      g.setEdge(t.id, target, {
        label: relation,
        weight: 0,
        style: 'stroke: blue; stroke-dasharray: 5, 5;',
        lineInterpolate: 'basis',
      });
    }
  }

  return g
}
/* eslint-enable */

export default {
  mixins: [filterMixin],

  data () {
    return {
      schema: annotationSchema,
      sentence: {
        tokens: []
      }
    }
  },

  computed: {
    svgURL() {
      return this.$http.options.root + '/graphs/' + this.$route.params.sentence
    },

    allTokens() {
      return this.sentence.tokens
    },

    visibleTokens() {
      return this.sentence.tokens.filter(t => !t.empty_token_sort)
    }
  },

  ready() {
    // addSvg()
  },

  route: {
    data({ to, abort }) {
      let sentenceParams = {
        sentence: to.params.sentence
      }

      return api.getSentence(sentenceParams).then((data) => {
        return {
          sentence: data
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  watch: {
    sentence(val) {
      // this.$nextTick(() => {
      //   this.drawGraph()
      // })
    }
  },

  methods: {
    addSvg() {
      let svg = d3.select("svg")
      let inner = d3.select("svg g")
      let zoom = d3.behavior.zoom().on("zoom", function() {
        inner.attr("transform", "translate(" + d3.event.translate + ")" + "scale(" + d3.event.scale + ")")
      })
      svg.call(zoom)
    },

    drawGraph() {
      let g = makeGraph(this.sentence)

      g.graph().transition = function(selection) {
        return selection.transition().duration(500)
      }

      /* eslint-disable */
      let render = new dagreD3.render()
      /* eslint-enable */

      d3.select("svg g").call(render, g)
    }
  }
}
</script>



